import { Component, OnInit } from '@angular/core';
import { SessaoService } from 'src/app/services/sessao.service';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import { Router } from '@angular/router';
import { Sessao } from 'src/app/models/sessao';
import { AlertController, Events } from '@ionic/angular';
import { resolve } from 'url';

@Component({
  selector: 'app-sessoes',
  templateUrl: './sessoes.page.html',
  styleUrls: ['./sessoes.page.scss'],
})
export class SessoesPage implements OnInit {

  sessoes = [];
  constructor(private sessaoService:SessaoService, private events: Events, private navExtra: NavExtrasService, private router: Router, private alertController: AlertController) { }

  async ngOnInit() {
    this.atualizarConteudos()
    this.events.subscribe('sessoes:atualizar', (atualizar) => {
      if (atualizar) this.atualizarConteudos();
    })
  }

  /**
   * Atualiza a lista caso desejado
   * @param event 
   */
  async refresh(event) {
    await this.atualizarConteudos();
    event.target.complete();
  }


  /**
   * Lista todas as sessões do Coordenador
   */
  async atualizarConteudos() {
    await new Promise((resolve) => setTimeout(() => resolve(), 200));
    this.sessoes = await this.sessaoService.buscarTodos();
  }

  /**
   * Abre a visualização de uma sessão
   * @param sessao 
   */
  visualizar(sessao:Sessao) {
    console.log(sessao);
    this.navExtra.set('sessao', sessao);
    this.router.navigateByUrl('coordenador/sessao');
  }

  
  /**
   * Abre a visualização de uma sessão
   * @param sessao 
   */
  editar(sessao:Sessao) {
    this.navExtra.set('sessao', sessao);
    this.router.navigateByUrl('coordenador/sessao-edicao');
  }
  /**
   * Exclui a sessão
   * @param sessao 
   */
  excluir(sessao:Sessao) {
    this.alertController.create({
      header: 'Excluir sessão',
      message: 'Deseja realmente excluir essa sessão?',
      buttons: [
        'Cancelar',
        {text:'Confirmar', handler:async () => {
          await this.sessaoService.remover(sessao.id);
          this.atualizarConteudos();
        }}
      ]
    }).then(a => a.present())
  }

}
